.phonebook{
    display: flex;
    justify-content: space-around;
}
.contacts{
    padding-left: 30px;
    border-left: 1px solid green;
    margin-left: 30px;
}


.div{
    width: 300px;
    height: 50px;
    position: relative;
    overflow: hidden;
}

.label{
    position: absolute;
    bottom: 0px;
    height: 100%;
    width: 100%;
    pointer-events: none;
    transition: all 0.3s ease;
}

.label::after{
    content: "";
    position: absolute;
    left: 0px;
    bottom: -1px;
    height: 100%;
    width: 100%;
    border-bottom: 5px solid #5fa8d3;
    transform: translateX(100%);
    transition: all 0.3s ease;

}

.span{
    position: absolute;
    bottom: 5px;
    left: 0px;
    transition: all 0.3s ease;
}

.input{
    height: 100%;
    width: 100%;
    border: none;
    outline: none;
    padding-top: 10px;
}


.input:focus + .label .span,
.input:valid + .label .span  {
    transform: translateY(-100%);
    border: none;
    position: absolute;
    font-size: 14px;
    color: #5fa8d3;
}

.input:focus + .label::after,
.input:valid + .label::after {
    transform: translateX(-0%);
} 


.deleteButton {
    background-color: #a81010; /* Green */
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
    border-radius: 5px;
}

.editButton{
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
    border-radius: 5px;
    padding: 10px
}

.cancelButton{
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
    border-radius: 5px;
    padding: 10px
}


@media only screen and (max-width: 900px) {
    .phonebook{
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 300px;
    }
    .contacts{
        padding-left: 0px;
        margin-left: 0px;
        border-left: none;
        border-top: 1px solid green;
    }
  }