  .loginButton {
    background-color: #3402e9; /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    width: 100%
  }

.div{
    width: 300px;
    height: 50px;
    position: relative;
    overflow: hidden;
}

.label{
    position: absolute;
    bottom: 0px;
    height: 100%;
    width: 100%;
    pointer-events: none;
    transition: all 0.3s ease;
}

.label::after{
    content: "";
    position: absolute;
    left: 0px;
    bottom: -1px;
    height: 100%;
    width: 100%;
    border-bottom: 5px solid #5fa8d3;
    transform: translateX(100%);
    transition: all 0.3s ease;

}

.span{
    position: absolute;
    bottom: 5px;
    left: 0px;
    transition: all 0.3s ease;
}

.input{
    height: 100%;
    width: 100%;
    border: none;
    outline: none;
    padding-top: 10px;
}


.input:focus + .label .span,
.input:valid + .label .span  {
    transform: translateY(-100%);
    border: none;
    position: absolute;
    font-size: 14px;
    color: #5fa8d3;
}

.input:focus + .label::after,
.input:valid + .label::after {
    transform: translateX(-0%);
} 
